div[id^="popup-check-"]
  max-width: 846px
  width: 100%
  background-color: #fff
  padding: 30px 25px 50px
  border: 1px solid #eee
  border-radius: 15px
  min-height: 500px
  .carousel__button
    display: none

.popup-check
  padding: 23px 32px 0 6px
  font-size: 18px
  &-head
    display: flex
    justify-content: space-between
    font-size: 18px
    padding-right: 29px
    padding-left: 4px
    padding-bottom: 22px
    border-bottom: 1px solid #e6e6e6
  &__item
    &-inner
      display: flex
      justify-content: space-between
    &-name
      & > .icon
        cursor: pointer

    &-check
      display: flex
      justify-content: space-between

    &-child
      display: none
  .form-check
    padding: 0
    & + .form-check
      margin-left: 30px
    &-input
      width: 21px
      height: 21px
      margin: 0
      border-color: #181818
      outline: none!important

.popup-check__item
  &-inner
    margin-bottom: 17px

.popup-check-toggle
  .icon
    font-size: 11px
    margin-left: 5px
  &--active
    & > .icon
      color: #e461af
      display: inline-block
      transform: rotate(180deg)

