.popup-check-child--active
  display: block!important
  position: relative
  &:after
    content: ""
    display: block
    width: 4px
    height: 100%
    background-color: #fff
    position: absolute
    top: 0
    left: -24px
  &:before
    content: ""
    display: block
    width: 2px
    height: 100%
    background-color: #f0f0f0
    border-left: 1px solid #f5f5f5
    position: absolute
    top: 0
    left: 10px
