.admin__filter
  display: flex
  flex-wrap: wrap

  &-wr
    margin-top: 23px
    display: flex
    justify-content: space-between
    align-items: flex-end

  &-reset
    font-family: 'Muller'
    border: none
    background: transparent
    padding: 0 5px
    color: #999999
    cursor: pointer
    transition: color .2s ease-in-out
    margin-bottom: 3px
    font-size: 12px
    flex-shrink: 0
    &:hover
      color: #222222

  &-sort
    border: 1px solid #eee
    padding: 6px 10px
    border-radius: 15px
    background: #fff
    font-size: 12px
    box-sizing: content-box
    text-decoration: none
    display: inline-block
    transition: padding .25s ease-in-out,box-shadow .15s ease-in-out,background-color .25s ease-in-out
    & > .icon
      font-size: 9px
      .path1:before,
      .path2:before
          transition: color .20s ease-in-out
    &:hover
      //& > .icon-sort-up
      //  & > .path1:before
      //    color: #dc3398
      //  & > .path2:before
      //    color: #c6c6c6
      //& > .icon-sort-down
      //  & > .path1:before
      //    color: #c6c6c6
      //  & > .path2:before
      //    color: #dc3398

  &-item
    position: relative
    font-size: 12px
    align-self: center
    flex-shrink: 0
    ::placeholder
      color: #333
      opacity: 1
    &--searching
      .admin__filter-list
        display: block
    &--active
      .admin__filter-input
        border-color: #cc3399
        background-color: #cc569a
        padding-right: 32px
        color: #fff
      .admin__filter-cancel
        display: block
    & + .admin__filter-item
      margin-left: 10px


  &-input, &-select
    border: 1px solid #eeeeee
    padding: 6px 10px
    border-radius: 15px
    font-size: 12px
    width: 0
    box-sizing: content-box
    transition: padding .25s ease-in-out, box-shadow .15s ease-in-out, background-color .25s ease-in-out
    & + .input-width-buffer
      position: absolute
      top: -1000px
      left: -1000px
      visibility: hidden
      white-space: pre
      border: 1px solid #eeeeee
      padding: 6px 10px
      display: inline-block
    &--id
      min-width: 13px
    &--platform
      min-width: 60px
    &--visibility
      min-width: 84px
      padding-right: 0
    &--type
      min-width: 21px
    &--status
      min-width: 38px
    &:focus,
    &:focus-visible
      outline: none
      border-color: rgb(239 127 187 / 50%)
      box-shadow: 0 0 0 0.25rem rgb(239 127 187 / 30%)

  .admin__filter-select
    height: 30px
    border: 1px solid #eee
    padding: 0px 10px
    border-radius: 15px
    font-size: 12px
    box-sizing: border-box
    padding-right: 27px
    background-size: 10px

  &-cancel
    width: 17px
    height: 17px
    background: transparent url(../img/filter-cancel.png) no-repeat center
    position: absolute
    top: 50%
    right: 6px
    margin-top: -8px
    display: none
    cursor: pointer

  &-list
    padding: 0 0 0 10px
    background-color: #fff
    position: absolute
    border-radius: 12px
    box-shadow: 0 3px 10px rgb(0 0 0 / 10%)
    margin-top: 2px
    font-size: 12px
    width: 224px
    display: none
    z-index: 3
    &-item + .admin__filter-list-item
      border-top: 1px solid #f5f5f5
    button
      white-space: pre
      background: 0 0
      padding: 7px 10px 6px 0
      width: 100%
      border: none
      text-align: left
      cursor: pointer
      &:hover > span
        border-bottom: 1px dashed




.admin__filter-item--sort
  .form-check
    padding-left: 0
    align-items: flex-start
    &-label
      font-size: 13px
      padding-left: 5px
      cursor: pointer
  .form-check-input
    margin-left: 0
  .form-check-input:checked
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='2' fill='%23999'/%3e%3c/svg%3e") !important
    border-color: #999!important
  .form-check-input:focus
    box-shadow: 0 0 0 .25rem #ebebeb!important

.admin__filter-list-item
  white-space: normal!important




.admin__filter-checkbox
  display: block
  position: relative
  padding-left: 25px
  margin-bottom: 12px
  cursor: pointer
  font-size: 14px
  user-select: none
  input
    position: absolute
    opacity: 0
    cursor: pointer
    height: 0
    width: 0
  .checkmark:after
    left: 8px
    top: 4px
    width: 5px
    height: 10px
    border: solid white
    border-width: 0 3px 3px 0
    transform: rotate(45deg)

.checkmark
  position: absolute
  top: 0
  left: 0
  height: 20px
  width: 20px
  background-color: #eee
  border-radius: 50%
  &:after
    content: ""
    position: absolute
    display: none

.admin__filter-checkbox:hover input ~ .checkmark
  background-color: #ccc

.admin__filter-checkbox input
  &:checked ~ .checkmark
    background-color: #cc569a
  &:checked ~ .checkmark:after
    display: block
