.section-banner
  margin-bottom: 35px

.banner__inner
  padding: 20px
  display: flex
  align-items: center
  justify-content: flex-end
  background-repeat: no-repeat
  background-position: left bottom
  background-color: #ffffff
  border: 1px solid #eeeeee
  border-radius: 20px
  height: 82px

.download-link
  height: 40px
  border-radius: 8px
  cursor: pointer

.download-link:not(:last-child)
  margin-right: 20px

.download-link.download-link--play-market
  width: 134px
  background: black url("../img/play-market-link.png") no-repeat center/contain

.download-link.download-link--app-store
  width: 120px
  background: black url("../img/app-store-link.png") no-repeat center/contain
