.carousel-items-list
  display: flex
  flex-wrap: wrap
  justify-content: space-between
  margin-bottom: 23px
  .stretched-link
    opacity: 0
    height: 0
    padding: 0


.section-categories
  .fix-col
    width: 176px
  .carousel-item
    opacity: 1
    margin-bottom: 17px
    &__inner
      width: 172px
      height: 150px
      box-shadow: 0 10px 20px rgb(0 0 0 / 10%)
    &-body
      padding: 10px
      justify-content: center
      align-items: center


.section-shops
  margin-bottom: 24px
  .fix-col
    width: 212px

.shop-list-section + .shop-list-section
  margin-top: 11px

.shop-list-items
  display: flex
  flex-wrap: wrap
  justify-content: space-between

.shop-list-letter
  font-size: 24px
  font-weight: 600
  padding-left: 10px
  margin-bottom: 14px

.shop-list-item
  background: #fff
  box-shadow: 0 10px 20px rgb(0 0 0 / 10%)
  padding: 5px 10px
  border-radius: 16px
  border: 1px solid #eee
  margin-bottom: 16px
  width: 212px
  display: flex
  justify-content: flex-start
  align-items: center
  transition: box-shadow .15s ease-in-out
  text-decoration: none
  &__img
    width: 28px
    height: 28px
    overflow: hidden
    border-radius: 50%
  &__name
    flex-grow: 1
    text-align: center
    padding: 0 10px
    font-weight: 600
    font-size: 16px
  &:hover
    box-shadow: 0 0 0 rgb(0 0 0 / 10%)

