.admin__filter-wr + .admin-accordion,
.overflow + .admin-accordion
  margin-top: 13px

.accordion
  &__item
    background-color: #ffffff
    border: 1px solid #eeeeee
    border-radius: 15px
    padding: 0px 2px 0px
    box-shadow: 0 0px 0px rgba(0,0,0,.0)
    transition: box-shadow .2s ease-in-out
    & + .accordion__item
      margin-top: 10px
    &--active
      box-shadow: 0 10px 30px rgba(0,0,0,.1)
      .entity__buttons
        border-bottom-right-radius: 0
  &__head
    display: flex
    justify-content: space-between
  &__body
    &-inner
      padding: 15px 7px 20px
      position: relative
      &:before
        content: ""
        display: block
        width: 100%
        height: 1px
        background: #eee
        position: absolute
        top: 0
        left: 0
    max-height: 0
    overflow: hidden
    transition: max-height 0.3s ease-out


.entity
  &__head
    display: flex
    justify-content: space-between
    &-left
      padding: 9px 0 7px 18px
      display: flex
      justify-content: space-between
    &-right
      position: relative
      &__top
        display: none
        height: 100%
      &__bottom
        height: 100%
        display: flex
        justify-content: space-between
      &.active
        .entity__head-right__bottom
          display: flex
          background: #fff
          z-index: 30
          box-shadow: 0 10px 30px rgb(0 0 0 / 10%)
          margin-top: 5px
          border: 1px solid #eee
          border-radius: 15px
          right: -3px
          height: 56px
        .entity__btn-more
          background: #f5f5f5
          color: #333
        .entity__number+.entity__number
          margin-left: 10px
          min-width: auto

  &__img
    position: relative
    width: 34px
    min-height: 39px
    &:before
      content: ""
      display: block
      width: 34px
      height: 34px
      position: absolute
      top: 0
      left: 0
      background: #f5f5f5 url("../img/camera.png") no-repeat 10px 10px
      border-radius: 50%
    &.entity__img-avatar
      &:before
        display: none
    &>img
      position: relative

    &:after
      content: ""
      position: absolute
      right: -2px
      top: 25px
      width: 12px
      height: 12px
      border: 2px solid #ffffff
      background-color: #0cc20c
      border-radius: 50px
    & + .entity-info
      margin-left: 14px
  &--deleted
    .entity__img:after
      background-color: red

  &__numbers
    //width: 174px
    display: flex
    justify-content: space-between
    padding: 9px 16px 0
    position: relative
    &:before, &:after
      content: ""
      display: block
      position: absolute
      top: 7px
      left: 0
      width: 1px
      height: 40px
      background-color: #eee
    &:after
      left: auto
      right: 0
  &__number
    min-width: 68px
    & + .entity__number
      margin-left: 20px
    &-title
      font-size: 12px
      color: #888888
      margin-bottom: 6px
    &-text
      font-size: 16px
      font-weight: 600


.entity-info
  &__title
    margin-top: 5px
    margin-bottom: 0
    font-size: 16px
    font-weight: 600
    color: #555555
  &__platform
    font-size: 12px
    color: #888888
  &__type
    padding: 1px 11px
    height: 15px
    font-size: 10px
    font-weight: 400
    color: #999999
    border-radius: 6px
    background-color: #f5f5f5
    margin-left: 10px

.entity-pin
  display: flex
  align-items: center
  background-color: #f5f5f5
  border-radius: 6px
  padding: 0 6px
  margin-left: 10px
  height: 15px
  &__item
    display: block
    width: 6px
    height: 6px
    background-color: #cccccc
    border-radius: 50px
    & + .entity-pin__item
      margin-left: 6px
    &--active
      background-color: #ef7fbb


.entity__buttons
  margin: 0px -2px 0 -1px
  display: flex
  border-top-right-radius: 15px
  border-bottom-right-radius: 15px
  overflow: hidden
  position: relative
  .icon-trash
    color: #ff6d6d
  .icon-refresh
    color: #33cc00
  .icon-unlock
    color: #3c0


.entity__btn
  display: flex
  background: 0 0
  padding: 0
  border: none
  align-items: center
  justify-content: center
  text-decoration: none
  font-size: 20px
  width: 47px
  color: #8c8c8c
  cursor: pointer
  transition: all .2s ease-in-out
  outline: none!important
  &:hover
    background: #f5f5f5
    color: #333


.accordion__btn .icon-chevron-down
  transition: transform .2s ease-in-out

.accordion__item--active .accordion__btn
  background: #f5f5f5
  color: #333
  & > .icon-chevron-down
    transform: rotate(180deg)


.entity__footer
  margin-top: 40px
  &-buttons
    flex-wrap: wrap
    display: flex
    justify-content: center
    & > *
      margin: 0 32px
  &-text
    text-align: center
    font-size: 14px
    color: #999999
    margin-top: 25px
    line-height: 1.6


#add-material-form
  .form-head
    padding: 0
    margin: 0
    margin-bottom: -10px
    &:after
      display: none
  .entity__footer
    display: none

  &.active
    .form-head
      padding-bottom: 21px
      margin-bottom: 16px
      &:after
        display: block
    .entity__footer
      display: block