@import 'reset'
@import 'fonts'

html
  background: #f5f5f5
  color: #222222

html, body, #page-wrapper
  height: 100%

#page-wrapper
  display: flex
  flex-direction: column
  & > main
    flex: 1 0 auto
    overflow: hidden
  & > footer
    flex: 0 0 auto

main > .container > section:last-child
  margin-bottom: 0

h1, h2, h3, h4, h5, h6
  margin-top: 0
  margin-bottom: 0.5rem

a
  color: #272727

p
  margin-top: 0
  margin-bottom: 1rem

strong
  font-weight: 700

dl, ol, ul
  margin-top: 0
  margin-bottom: 1rem

ol, ul
  padding-left: 2rem

li
  display: list-item
  text-align: -webkit-match-parent

.list-unstyled
  padding-left: 0
  list-style: none

hr
  background: #eee
  border: none
  height: 1px

body, input, button
  font-family: 'Nunito Sans', sans-serif

body
  font-size: 14px
  line-height: 1.2

input
  font-size: 14px
  box-shadow: none
  &:focus, &:focus-visible
    box-shadow: none

.d-flex
  display: flex

.justify-content
  &-center
    justify-content: center
  &-between
    justify-content: space-between

.d-none
  display: none!important

.align-items-center
  align-items: center

.content
  background: #fff
  border-radius: 15px
  margin-top: 17px
  border: 1px solid #eeeeee
  padding: 17px 9px 40px

.container
  //max-width: 1170px
  max-width: 1150px
  padding: 0 15px
  margin: 0 auto

.text-center
  text-align: center

.row
  display: flex
  justify-content: space-between
  .main
      flex-grow: 1
      max-width: 850px
  .sidebar
    width: 262px
    margin-left: 30px

.stretched-link::after
  position: absolute
  top: 0
  right: 0
  bottom: 0
  left: 0
  z-index: 1
  pointer-events: auto
  content: ""
  background-color: rgba(0,0,0,0)


.page-heading__title
  font-size: 24px
  font-weight: 700
.section-title
    font-size: 24px
    font-weight: 700
    padding-left: 5px

.page-heading
  text-align: center
  margin: 26px 0
  &__inner
    margin: 0 auto
    width: 100%
    max-width: 690px
    + hr
      margin-top: 40px
      margin-bottom: 40px
  &__subtitle
    font-size: 16px
    font-weight: 600
    margin-top: 13px
    margin-bottom: 0

.add-fields
  display: none
  &--active
    display: block


@import "btn"
@import "header"
@import "menu"
@import 'admin-filter'
@import 'sidebar'
@import 'accordion'
@import 'form'
@import 'pagination'
@import 'popup-check'
@import 'carousel'
@import 'banner'
@import 'pages'
@import 'popups'
@import 'media'
@import 'footer'