.fancybox__content>.carousel__button.is-close
  right: -29px
  top: -29px
  & > svg
    stroke: #c86da6
    width: 25px
    height: 25px
    filter: none

.fancybox__backdrop
  background: rgb(51 51 51 / 80%)

.fancybox-rounded
  .fancybox__content
    border-radius: 15px
    max-width: 440px
    padding: 26px 20px
    color: #181818
  :focus
    outline: none!important

.popup
  &-title
    font-size: 24px
    font-weight: 700
    text-align: center
    &-row--2
      font-family: 'Muller'
      font-size: 14px
      font-weight: 400
      margin-top: 7px
  &-text
    font-size: 14px
    color: #555555
    margin-top: 24px
  &-buttons
    display: flex
    justify-content: center
    .btn
      margin-top: 28px
      font-size: 16px
      padding: 0.375rem 1.2rem
  &-content
    & > form
      margin-top: 26px
      .form-row+.form-row>*
        margin-top: 14px
      .form-col
        padding: 0
      .form-control
        font-size: 14px
        padding: 6px 14px

#popup-login, #popup-register, #popup-forgot
  .popup-buttons
    justify-content: space-between
    align-items: center
    .btn
      margin-top: 28px
      font-size: 16px
      padding: 0.375rem 1.2rem
      width: auto
      max-width: none
      flex-grow: 1
      height: 36px
    button.btn
      margin-right: 10px


#popup-contacts, #popup-b2b
  max-width: 600px

.popup-contacts-list
  margin-top: 26px
  & > ul
    margin: 0
    padding: 0
    display: flex
    flex-wrap: wrap
    & > li
      position: relative
      display: flex
      margin-right: 30px
      margin-bottom: 14px
      & > a
        border: 1px solid #f5f5f5
        border-radius: 10px
        padding: 4px 18px 4px 15px
        display: flex
        align-items: center
        text-decoration: none
        & > img
          margin-right: 10px
      .icon-arrow-right
        margin-left: 10px
        border: 1px solid #f5f5f5
        border-radius: 10px
        padding: 7px 6px 0
.popup-team
  margin-top: 13px
  &-title
    font-size: 20px
    font-weight: 600
  &-list
    display: flex
    flex-wrap: wrap
    margin-top: 25px
  &-item
    width: 178px
    margin-right: 15px
    border: 1px solid #ededed
    border-radius: 25px
    text-align: center
    padding-bottom: 13px
    &__img
      height: 129px
      border-bottom: 1px solid #ededed
      position: relative
      & > img
        display: block
        position: absolute
        bottom: 0
        left: 50%
        transform: translate(-50%, 0)
    &__links
      display: flex
      justify-content: center
      align-items: center
      & > a
        text-decoration: none
        box-shadow: 0 0 10px rgba(0,0,0,.1)
        display: inline-block
        text-align: center
        height: 26px
        width: 31px
        padding-top: 5px
        border-radius: 10px
        & + a
          margin-left: 12px
        & > img
          display: inline-block
    &__name
      font-size: 16px
      font-weight: 600
      margin-top: 7px
      & > span
        display: block
        font-size: 12px
        color: #777777
    &__info
      color: #181818
      font-size: 14px
      margin-top: 10px
      margin-bottom: 10px

#popup-b2b
  //height: 700px
.popup-banner
  margin-top: 46px


.popup-banner__list
  display: flex
  margin-top: 10px
  flex-wrap: wrap

.popup-banner__title
  font-size: 16px
  font-weight: 600
  padding-left: 5px

.popup-banner__item
  background: #f5f5f5
  display: flex
  justify-content: center
  align-items: center
  font-size: 16px
  font-weight: 800
  border: 1px solid #eeeeee
  border-radius: 10px
  margin-right: 16px
  margin-bottom: 16px
  width: 144px
  height: 210px
  &--active
    border: 2px solid #ef7fbb

.popup-banner__item--wide
  height: 45px
  width: 100%

.popup-banner__descr
  text-align: center
  margin-top: -6px
  & + .popup-banner__title
    margin-top: 22px