.owl-stage-outer
  //padding: 20px 20px 35px
  //margin: -20px -20px 0px
  padding: 20px 0 35px 20px
  margin: -20px 0 0 -20px

.owl-carousel
  display: block!important
  cursor: grab

  &-platforms
    .owl-stage-outer
      //padding: 20px 20px 26px
      //margin: -20px -17px -26px
  &-promo-codes, &-promotions
    .owl-stage-outer
      padding: 20px 0px 35px 20px
      margin: -20px 0px 0px -20px

.owl-item.active
  .carousel-item
    opacity: 1
    &__inner
      box-shadow: 0 10px 20px rgb(0 0 0 / 10%)

.carousel-item
  opacity: 0
  transition: opacity .2s ease-in-out
  &__inner
    position: relative
    background: #fff
    border-radius: 15px
    transition: box-shadow .15s ease-in-out
    display: flex
    flex-direction: column
    justify-content: space-between
    border: 1px solid #eeeeee
    &--pinned
      border: 2px solid #deb1cd
  &:hover
    .carousel-item__inner
      box-shadow: 0px 0px 0px rgb(0 0 0 / 10%)
  &-head
    padding: 10px 20px
    border-bottom: 1px solid #f5f5f5
    font-size: 20px
    flex-shrink: 0
    text-align: center
    height: 62px
    display: flex
    align-items: center
    justify-content: center
  &-body
    padding: 36px 20px 20px
    flex-grow: 1
    display: flex
    text-align: center
    align-items: center
    justify-content: center
    flex-direction: column
    .btn
      max-width: 130px
      padding: 6px 12px
      font-size: 14px
  &-description
    display: flex
    vertical-align: middle
    align-items: center
    flex-direction: column
    max-height: 100%
    overflow: hidden
    & > p
      margin: 0
  &-footer
    font-size: 16px
    flex-shrink: 0
    border-top: 1px solid #f5f5f5
    padding: 20px
    text-align: center
    & > .d-flex
      align-items: center
  &__img
    text-align: center
    & > img
      width: auto!important
  &-code
    &__inner
      background-color: #f5f5f5
      width: 207px
      border: 1px dashed #f252a7
      padding: 9px
      border-radius: 11px
  &__date
    background: #eeeeee
    font-size: 14px
    border-radius: 9px
    padding: 3px 8px
    border: 1px solid #e5e5e5
  &__help
    &-btn
      cursor: pointer
      background: #eeeeee
      border-radius: 50%
      padding: 4px
      border: 1px solid #e5e5e5
      display: flex
      justify-content: center
      align-items: center
      color: #222
    .icon
      font-size: 14px
      color: #222

.carousel-item-category
  width: 172px
  .carousel-item__inner
    height: 234px
  .carousel-item-body
    display: flex
    flex-direction: column
    justify-content: space-between
    color: #333333
    text-align: center
  .carousel-item-description
    height: 50px

.carousel-item-platform
  width: 144px
  .carousel-item__inner
    height: 180px
  .carousel-item-body
    padding: 10px
    min-height: 124px
    max-height: 124px
  .carousel-item-footer
    padding: 10px 20px
    height: 54px
    white-space: nowrap
    text-overflow: ellipsis
    overflow: hidden
    display: flex
    justify-content: center
    align-items: center
    & > a
      text-decoration: none

.carousel-item-name
  font-weight: 600

.carousel-item-promo-code
  width: 242px
  .carousel-item__inner
    height: 342px
  .carousel-item-head
    padding: 10px
    height: 87px
  .carousel-item-body
    font-size: 16px
    font-weight: 600
    justify-content: space-between
    padding: 20px
    &__inner
      height: 73px
      display: flex
      align-items: center
    & > p
      height: 70px
    .btn
      max-width: 180px
  .carousel-item__img
    display: flex
    align-items: center
    justify-content: center
    height: 82px
  .carousel-item-footer
    border: none
    padding: 16px 20px 20px

.carousel-item-promotion
  width: 242px
  .carousel-item__inner
    height: 331px
  .carousel-item-head
    padding: 10px
    height: 87px
  .carousel-item-footer
    border: none
    padding: 18px 20px 20px


.carousel-item-code
  &__help
    font-size: 12px
    &:before
      content: "нажмите, чтобы скопировать"
      padding: 6px 0 0
      display: inline-block
  &--copied
    .carousel-item-code__help
      &:before
        content: "скопировано"
        white-space: nowrap
      &:after
        color: #c86da6
        content: "\e90d"
        font-family: 'icomoon' !important
        speak: never
        font-style: normal
        font-weight: normal
        font-variant: normal
        text-transform: none
        line-height: 1
        /* Better Font Rendering =========== */
        -webkit-font-smoothing: antialiased
        -moz-osx-font-smoothing: grayscale
        position: relative
        top: 2px
        padding-left: 1px
        font-size: 13px

.carousel-item-more
  &__help
    font-size: 12px
    &:before
      content: "нажмите, чтобы узнать подробнее"
      padding: 8px 0 0
      display: inline-block
      white-space: nowrap


.link-all
  display: inline-block
  margin-top: 10px
  font-size: 12px
  padding: 0 5px
  text-decoration: underline
  &:hover
    text-decoration: none

.carousel-item-show-all
  width: 160px
  .carousel-item__inner
    justify-content: center
    align-items: center
    box-shadow: none!important
    background: transparent!important
    border: none

.show-all
  text-decoration: none
  .icon
    border-radius: 50%
    text-align: center
    display: flex
    justify-content: center
    align-items: center
    background: #fff
    width: 58px
    height: 58px
    box-shadow: 4px 0px 10px rgb(0 0 0 / 10%)
    margin: 0 auto 13px
    transition: box-shadow .15s ease-in-out
    border: 1px solid #eeeeee
    &:before
      font-size: 23px
  &:hover
    .icon
      box-shadow: 0px 0px 0px rgb(0 0 0 / 10%)