.pagination-wr
  display: flex
  justify-content: center
  margin-top: 39px
  .limit
    margin-left: 29px
    position: relative
    &:before
      content: ""
      display: block
      width: 1px
      height: 100%
      background: #e5e5e5
      position: absolute
      left: -14px
      top: 0

.pagination
  display: flex
  padding-left: 0
  list-style: none
  margin-bottom: 0

.page
  &-link
    padding: 3px 6px
    position: relative
    min-width: 32px
    text-align: center
    display: block
    font-weight: 700
    font-size: 12px
    color: #555
    text-decoration: none
    background-color: #fff
    border: 1px solid #eee
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out
    border-radius: 8px
    &:hover,
    &--active
      background-color: #ef7fbb
      color: #fff
      border-color: #dd75ad
  &-item
    &+.page-item
      margin-left: 4px

