
.header
  padding: 20px 0
  &__inner
    display: flex
    align-items: center
    justify-content: space-between
  &__logo
    margin-right: 20px
    & > img
      display: block
      height: 35px
  &__search
    width: 100%
    max-width: 585px
    position: relative
    margin: 0 20px
  input
    padding: 10px 16px
    width: 100%
    height: 35px
    font-family: 'Nunito Sans', sans-serif
    font-size: 14px
    font-weight: 400
    color: #555555
    background-color: #ffffff
    border: 1px solid #eeeeee
    border-radius: 10px
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1)

    &::placeholder
      font-family: 'Nunito Sans', sans-serif
      font-size: 14px
      font-weight: 400
      color: #555555

#search-reset
  position: absolute
  top: 9px
  right: 15px
  font-size: 17px
  color: #eeeeee
  cursor: pointer
  transition: color .2s ease-in-out
  &:hover
    color: #cccccc

#search-list
  display: none
  position: absolute
  margin: 5px 0 0
  width: 100%
  &.search-list--visible
    display: block

.search-list__inner
  box-shadow: 0 5px 20px rgb(0 0 0 / 10%)
  background: #fff
  border-radius: 10px
  overflow: hidden
  padding: 0

.search-list__item
  &-name
    overflow: hidden
    white-space: pre
    text-overflow: ellipsis
    color: #555555
  &-type
    font-size: 12px
    color: #999999
  & > a
    display: flex
    justify-content: space-between
    padding: 9px 18px
    min-height: 41px
    text-decoration: none
    transition: background-color .2s ease-in-out
    align-items: center
    &:hover
      background: #f5f5f5
  & + .search-list__item
    & > a
      border-top: 1px solid #eeeeee


.header__account
  position: relative
  height: 35px
  margin-top: -20px

  &-inner
    transition: all .2s ease-in-out
    position: relative
    border-radius: 15px
  &-visible
    display: flex
    align-items: center
    justify-content: flex-start
    cursor: default
    position: relative
    z-index: 2
    padding: 10px 19px
  &-hidden
    visibility: hidden
    opacity: 0
    transition: .2s ease-in-out
    position: relative
    z-index: 2
    border-bottom-right-radius: 15px
    border-bottom-left-radius: 15px
    overflow: hidden
    border-top: 1px solid #eee
    & .menu-list
      border: none
      border-radius: 0
  &:hover
    .header__account-inner
      background: #fff
      box-shadow: 0 10px 30px rgb(0 0 0 / 10%)
      &:before
        opacity: 1
    .header__account-visible
      text-decoration: underline
    .header__account-hidden
      visibility: visible
      opacity: 1
  .header__account-text
    color: #111111
    font-size: 14px
    font-weight: 400
    white-space: pre


.header__account-img
  margin-right: 10px

.avatar
  width: 35px
  height: 35px
  display: flex
  align-items: center
  justify-content: center
  border-radius: 50px
  background-color: #cccccc

  &--green
    background-color: #82e78e!important
  &--pink
    background-color: #f252a7!important
  &--orange
    background-color: #ecb85d!important
  &--blue
    background-color: #68bfef!important
  &--white
    background-color: #fff!important


