@media (max-width: 1119px)
  .entity__head-right__top
    display: flex
    margin-left: 10px

  .row .sidebar
    margin-left: 20px

  .entity__head-right__bottom
    display: none
    position: absolute

@media (max-width: 991px)
  .form-col-6
    width: 100%
  .form-col-4
    width: 100%

  .form-pinned
    border-bottom: 1px solid #eee
    padding-bottom: 15px
    .form-col-4:nth-child(2):after,
    .form-col-4:nth-child(2):before
      display: none
    .form-row
      margin-top: 15px
      border-top: 1px solid #eeeeee
      padding-top: 15px
      .form-col-4 + .form-col-4
        margin-top: 15px



@media (max-width: 767px)
  .container
    padding: 0 10px

  .header__logo
    display: none

  .header__search
    margin: 0 10px 0 0

  .header__account
    display: none

  #burger
    display: block

  .menu-section--mobile
    display: block

  .page-heading
    margin: 10px 0

  .page-heading__subtitle
    margin-top: 16px

  .page-heading__inner+hr
    margin-top: 22px
    margin-bottom: 22px

  .section-title
    margin-bottom: 13px

  .owl-carousel-promo-codes .owl-stage-outer, .owl-carousel-promotions .owl-stage-outer, .owl-stage-outer
    margin-right: -20px

  .sidebar
    display: none

  .download-link
    display: none

  .entity__head-right__bottom .accordion__btn
    display: none

  .footer__inner
    flex-wrap: wrap
    padding-bottom: 10px
    & > p
      margin-bottom: 10px

  .entity__footer-buttons
    .btn + .btn
      margin-top: 10px

  .fancybox__content>.carousel__button.is-close
    right: -8px
    top: -37px

  .section-categories .carousel-item-category
    width: 50%
    padding: 0 7px
  .section-categories .carousel-item__inner
    width: 100%

  .footer__links
    display: none


@media (max-width: 545px)
  .shop-list-item
    width: 100%
    .shop-list-item__name
      text-align: left
      padding-left: 15px
  .popup-team-item
    width: 100%
    margin: 0
    & + .popup-team-item
      margin-top: 20px
  .popup-banner__list
    justify-content: space-between

  .popup-banner__item
    margin: 0
    max-width: 30%
    height: 150px
  .popup-banner__item--wide
    max-width: 100%
    height: 45px
  .popup-banner__descr
    margin-top: 5px

  .admin__filter-wr
    margin-top: 13px
  .admin__filter-item
    margin-right: 10px
    margin-top: 10px
    &+.admin__filter-item
      margin-left: 0
  .admin__filter-list
    width: 200px
