.menu-list
  background-color: #ffffff
  border: 1px solid #eeeeee
  border-radius: 15px
  overflow: hidden
  padding: 0
  margin-bottom: 0


.menu-section
  overflow: hidden
  & + .menu-section
    margin-top: 20px
  &__title
    margin-bottom: 10px
    padding-left: 20px
    font-size: 11px
    font-weight: 400
    color: #888888
    text-transform: uppercase
  &--mobile
    display: none


.menu-item
  &:first-child
    border-top-left-radius: 15px
    border-top-right-radius: 15px
  &:last-child
    border-bottom-left-radius: 15px
    border-bottom-right-radius: 15px
    .menu-link:after
      display: none
  & + .menu-item
    .menu-link:before
      content: ""
      display: block
      width: 100%
      height: 2px
      position: absolute
      top: 0
      left: 0
      background: #fff
      z-index: 2
      transform: translate(0, 0)
      transition: background-color .2s ease-in-out, transform .2s ease-in-out


.menu-link
  position: relative
  padding: 15px 20px
  display: flex
  text-decoration: none
  justify-content: space-between
  align-items: center
  transition: background-color .2s ease-in-out
  &:after
    content: ""
    display: block
    width: 100%
    height: 1px
    margin-left: 20px
    position: absolute
    background: #eeeeee
    bottom: 0
    left: 0
    transition: background-color .2s ease-in-out
    z-index: 2
  &--active,
  &:hover
    background-color: #f5f5f5
    &:before
      background: #f5f5f5 !important
      transform: translate(0, -2px) !important
    &:after
      background: #f5f5f5
  &__text
    font-size: 14px
    font-weight: 600
    color: #2b2b2b
    text-decoration: none
    padding: 4px 10px 4px 0
    min-height: 24px
  &__count
    padding: 5px 8px
    font-size: 12px
    font-weight: 700
    color: #333333
    text-align: center
    background-color: #eeeeee
    border-radius: 12px
    &--colored
      color: #ffffff
      background-color: #ef7fbb

  .icon-plus-circle
    color: #f252a7
    font-size: 20px


.header__account
  .menu-link
    padding: 8px 18px
    white-space: pre
    &__text
      padding-right: 0
    &:after
      margin-left: 18px














.fancybox-slideLeftIn
  animation: 0.25s ease both fancybox-slideLeftIn
.fancybox-slideLeftOut
  animation: 0.15s ease both fancybox-slideLeftOut
@keyframes fancybox-slideLeftIn
  from
    opacity: 0
    transform: translate(-100%, 0)

  to
    opacity: 1
    transform: translate(0, 0)

@keyframes fancybox-slideLeftOut
  to
    opacity: 0
    transform: translate(-100%, 0)



#menu-mobile
  align-self: flex-start
  flex-grow: 1
  background-color: #f5f5f5
  padding: 15px 15px 80px 15px
  display: none
  width: 315px
  position: relative
  &:after
    content: ""
    width: 115px
    height: 26px
    background-image: url(../img/logo.svg)
    background-repeat: no-repeat
    background-position: center
    filter: grayscale(1)
    position: absolute
    bottom: 15px
    left: 50%
    transform: translate(-50%, 0)
    opacity: .3
  .carousel__button
    display: none
  .header__account-inner
    margin-bottom: 20px
    background: #fff
    border: 1px solid #eee
  .header__account-text u
    text-decoration: none!important
  .header__account-hidden
    opacity: 1
    visibility: visible
    height: 0
    transition: height .25s ease-in-out
    .menu-list
      border-radius: 15px
      &:first-child
        border-top-left-radius: 0
        border-top-right-radius: 0

#menu-mobile .header__account-inner
  overflow: hidden
  &.active
    .header__account-hidden
      height: 111px
    .header__account-visible:before
      transform: rotate(180deg)
#menu-mobile .header__account-visible
  cursor: pointer
  &:before
    content: ""
    display: block
    width: 20px
    height: 20px
    position: absolute
    top: 18px
    right: 17px
    background-image: url(../img/chevron-down.svg)
    background-position: center
    background-size: contain
    transition: transform .2s ease-in-out

.fancybox-menu
  .fancybox__slide
    padding: 0
    &::before, &::after
      display: none

.fancybox__container + .fancybox__container
  z-index: 1210



#burger
  z-index: 1200
  height: 35px
  width: 35px
  background-color: #fff
  border-radius: 50%
  border: none
  padding: 0
  flex-shrink: 0
  box-shadow: 0 4px 20px rgb(0 0 0 / 10%)
  cursor: pointer
  display: none
  outline: none!important
  transition: box-shadow .2s ease-in-out
  &:hover
    box-shadow: 0 0 0 rgb(0 0 0 / 10%)
.burger__inner
  position: relative
  display: block
  width: 14px
  height: 12px
  background: transparent
  border-top: 2px solid #252525
  border-bottom: 2px solid #252525
  border-left: none
  border-right: none
  font-size: 0
  transition: all .25s ease-in-out
  margin: 0 auto
  &:before,
  &:after
    content: ''
    display: block
    width: 100%
    height: 2px
    position: absolute
    top: 50%
    left: 50%
    background: #252525
    transform: translate(-50%, -50%)
    transition: transform .25s ease-in-out

.with-fancybox
  .burger__inner
    border-color: transparent
    &:before
      transform: translate(-50%, -50%) rotate(45deg)
    &:after
      transform: translate(-50%, -50%) rotate(-45deg)
